* {
    font-family: Helvetica, Arial, sans-serif;
}
body {
    padding: 60px;
}

input {
    width: 300px;
}
.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin: 0 10px 10px 0;
    overflow: hidden;
    width: 300px;
}
.card p {
    padding: 5px;
    text-align: center;
}
.card img {
    max-height: 300px;
    width: 300px;
}
